.App {
  font-family: sans-serif;
  text-align: center;
}
/*Keyboard and Button Css*/
/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
.keybord {
  box-shadow: -3px -3px 5px rgb(63, 63, 63), 3px 3px 5px black;
  width: 680px;
  margin: 200px auto 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px;
  border-radius: 10px;
  background-color: rgb(69, 62, 62);
}
.row {
  margin: 5px;
  user-select: none;
}
button {
  width: 50px;
  height: 50px;
  font-weight: bold;
  margin: 0 4px;
  border: none;
  background-color: rgb(57, 47, 47);
  color: white;
  box-shadow: -2px -2px 4px rgb(63, 63, 63), 2px 2px 4px black;
  border-radius: 5px;
  cursor: pointer;
}
.backSpace button {
  width: 150px;
  height: 50px;
  font-weight: bold;
  margin: 0 4px;
  border: none;
  background-color: rgb(57, 47, 47);
  color: white;
  box-shadow: -2px -2px 4px rgb(63, 63, 63), 2px 2px 4px black;
  border-radius: 5px;
  cursor: pointer;
}

.row4,
.backspace,
.space {
  display: flex;
  align-items: center;
  justify-content: center;
}
.keyboard .backspace,
.space {
  color: white;
  font-weight: bold;
  cursor: pointer;
  /* user-select: none; */
}
.backspace,
.space {
  border-radius: 5px;
  box-shadow: -2px -2px 4px rgb(63, 63, 63), 2px 2px 4px black;
}
.keybord .space {
  width: 300px;
  height: 50px;
}
.keybord .backspace {
  width: 100px;
  height: 50px;
  margin-left: 15px;
}
button:hover {
  width: 70px;
  height: 70px;
}
button:active {
  background-color: rgb(30, 27, 27);
  box-shadow: inset -2px -2px 4px rgb(63, 63, 63);
}
